import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-nav",
  templateUrl: "./nav.component.html",
  styleUrls: ["./nav.component.scss"]
})
export class NavComponent implements OnInit {
  phase: any;
  side: boolean;
  constructor() {}
  ngOnInit() {}
  show(side) {
    this.side = true;

    this.phase = side;
    const timer = setTimeout(() => {
      this.side = false;
    }, 6000);
  }
}
