import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { HttpHeaders } from "@angular/common/http";
const httpOptions = {
  headers: new HttpHeaders({
    Authorization: "5ab0aa0c0f94f5a366b623ffbe6219dda950d462"
  })
};
@Component({
  selector: "app-update",
  templateUrl: "./update.component.html",
  styleUrls: ["./update.component.scss"]
})
export class UpdateComponent implements OnInit {
  readme: any;
  constructor(private http: HttpClient) {}

  ngOnInit() {}
  create() {
    const data = {
      message: "my commit message",
      committer: {
        name: "Manojkumar Muthukumar",
        email: "creativecoderone@gmail.com"
      },
      content: btoa("Hello Baby Boy")
    };
    this.http
      .put(
        "https://api.github.com/repos/creativecodeone/contents/poster/zl1k56.txt",
        data,
        httpOptions
      )
      .subscribe(da => {
        this.readme = da;
        console.log(this.readme);
      });
  }
}
